@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Lora&family=Poppins:wght@400;700&family=Roboto:wght@400;700&family=Varela+Round&display=swap');

:root{
    --primary-text-color: #9cbc1c;
    --seccondary-text-color: #000000;
    --link-color: #1f73c8;
    --padding-inline-section: 20px;
    --name-size: 150px;
    --heading-size: 40px;
    --sub-heading-size: 30px;
    --content-size: 20px;
}

a{
    text-decoration: none;
    color: var(--link-color);
}

body{
    font-family: 'lora';
}

hr{
    background-color: var(--primary-text-color);
    border: none;
    margin: 20px;
    height: 2px;
}

.flex{
    display: flex;
    align-items: center;
}

.container{
    max-width: 1180px;
    margin-inline: auto;
    overflow: hidden;
    padding: var(--padding-inline-section);
}

.hero{
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;
}

.name{
    font-family: 'Dancing Script';
    font-size: var(--name-size);
    text-align: center;
    color: var(--primary-text-color);
    width: 80%;
}

.profile-img{
    width: 20%;
    box-shadow: 0 0 ;
}

.description{
    margin-top: 10px;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size: var(--content-size);
    /* row-gap: 4px;  */
    gap: 10px 20px;
}

.desc-item{
    display: flex;
    align-items: center;
    gap: 10px;
}

.heading{
    margin-top: 20px;
    font-size: var(--heading-size);
    color: var(--seccondary-text-color);
}

.primary-ul{
    margin: 5px ;
    font-size: var(--sub-heading-size);
    color: var(--primary-text-color);
}

.seccondary-ul{
    margin: 4px ;
    font-size: var(--content-size);
    color: var(--seccondary-text-color);
}












/* Media Queries */

/* 1024 px */
@media screen and (max-width: 1024px) {
    :root{
        --primary-text-color: #9cbc1c;
        --seccondary-text-color: #000000;
        --link-color: #1f73c8;
        --padding-inline-section: 20px;
        --name-size: 120px;
        --heading-size: 37px;
        --sub-heading-size: 27px;
        --content-size: 17px;
    }

    .hero{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .name{
        font-family: 'Dancing Script';
        font-size: var(--name-size);
        text-align: center;
        color: var(--primary-text-color);
        width: 70%;
    }
    
    .profile-img{
        width: 30%;
    }
    
}


/* 480px */
@media screen and (max-width: 480px) {
    :root{
        --primary-text-color: #9cbc1c;
        --seccondary-text-color: #000000;
        --link-color: #1f73c8;
        --padding-inline-section: 20px;
        --name-size: 85px;
        --heading-size: 35px;
        --sub-heading-size: 25px;
        --content-size: 15px;
    }

    .hero{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .name{
        font-family: 'Dancing Script';
        font-size: var(--name-size);
        text-align: center;
        color: var(--primary-text-color);
        width: 70%;
    }
    
    .profile-img{
        width: 30%;
    }
    
}

